<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
// import IncreaseLimitModal from '../components/newModals/IncreaseLimitModal.vue'

export default defineComponent({
	data: () => ({
		auction: null,
		currTimestamp: +Date.now(),
		timerEndTimestamp: +Date.now(),
		showHighlight: false,
		coins: null,
		// showIncreaseLimitModal: false
	}),
	setup() {
	},
	// components: { IncreaseLimitModal }
	computed: {
		clientId() {
			return this.$store.getters.profile.id;
		},
		auctionRunning() {
			return this.auction?.type === "live";
		},
		auctionFinished() {
			return this.auction?.type === "finished";
		},
		// youCanBuy(){
		//     return this.auction ? Math.min((this.auction.youCanBuy || 0) + this.auction.limit, (this.auction.maxUSDBuyAmount || Infinity) / this.startPrice) : 0;
		// },
		timeLeft() {
			let totalTime = Math.floor((this.timerEndTimestamp - this.currTimestamp) / 1000);
			if (totalTime <= 0) {
				if (this.auction?.type == "announce") {
					this.startAuction();
				}
				return "00:00";
			}
			let hours = Math.floor(totalTime / 3600);
			let otherTime = totalTime % 3600;
			let minutes = Math.floor(otherTime / 60);
			let seconds = Math.floor(otherTime % 60);
			return `${hours}:${minutes >= 10 ? minutes : ("0" + minutes)}:${seconds >= 10 ? seconds : ("0" + seconds)}`;
		},
		shareSize() {
			return 20;
		},
		onePercentPrice() {
			return this.auction?.onePercentPrice;
		},
	},
	methods: {
		// increaseLimit() {
		//     this.showIncreaseLimitModal = true
		// },
		setupTimers() {
			clearInterval(this.timerInterval);
			this.timerEndTimestamp = this.auctionRunning || this.auctionFinished ? this.auction?.presaleEndTimestamp : this.auction?.presaleStartTimestamp;
			this.timerInterval = setInterval(() => {
				this.currTimestamp = Date.now();
			}, 1000);
		},
		startAuction() {
			if (!this.auctionRunning) {
				this.auctionRunning = true;
				clearInterval(this.timerInterval);
				this.loadData();
			}
		},
		async buy() {
			this.$store.dispatch("setShowPaymentCryptostartModule", { value: false })
			this.$store.dispatch("clearPaymentCryptostartData");
			this.$nextTick(() => {
				this.$store.dispatch("setShowPaymentCryptostartModule", { value: true});
			});
		},
		async loadData() {
			try {
				const serverData = (await this.$store.dispatch("getCryptostartClosedPresaleParameters")).coins;
				this.auction = JSON.parse(JSON.stringify(serverData[0]));
				if (serverData.length > 0) {
					this.auction = JSON.parse(JSON.stringify(serverData[i]));
					this.setupTimers();
				}
				// // console.log(this.coins, this.auction)
			}
			catch (e) {
				this.$error(e);
			}
		}
	},
	created: function () {
		this.timerInterval = null;
	},
	async mounted() {
		await this.loadData();
		const exchangeClosedAuctionPageOpened = JSON.parse(localStorage.exchangeClosedAuctionPageOpened ?? null);
		this.showHighlight = true; //(exchangeClosedAuctionPageOpened != true)
		if (exchangeClosedAuctionPageOpened != true) {
			localStorage.exchangeClosedAuctionPageOpened = true;
			window.dispatchEvent(new CustomEvent("exchangeClosedAuctionPageOpened", {
				detail: {
					value: localStorage.getItem("exchangeClosedAuctionPageOpened")
				}
			}));
		}
		setTimeout(() => {
			this.showHighlight = false;
		}, 2000);


		this.buy()
	},
	unmounted() {
		clearInterval(this.timerInterval);
	},
})
</script>

